import React, { useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';

function TemperatureChart({ temperatures }) {
    // Rfrence  l'lment canvas
    const chartRef = useRef(null);

    // Variable pour stocker l'instance du graphique
    let chartInstance = null;

    // Utilisation de useEffect pour crer et mettre  jour le graphique
    useEffect(() => {
        // Vrifier si l'lment canvas et les donnes de temprature sont disponibles
        if (chartRef.current && temperatures.length > 0) {
            // Vrifier s'il existe une instance prcdente de Chart.js
            if (chartInstance) {
                // Si oui, la dtruire pour viter les problmes de rutilisation du canvas
                chartInstance.destroy();
            }

            // Obtenir le contexte 2D du canvas
            const ctx = chartRef.current.getContext('2d');

            // Crer une nouvelle instance de Chart.js
            // eslint-disable-next-line react-hooks/exhaustive-deps
            chartInstance = new Chart(ctx, {
                type: 'line', // Type de graphique (ligne)
                data: {
                    labels: temperatures.map((temp, index) => `Hour ${index}`), // Libells pour l'axe des X
                    datasets: [{
                        label: 'Temperature (C)', // tiquette du dataset
                        data: temperatures, // Donnes de temprature
                        fill: false, // Ne pas remplir la zone sous la ligne
                        borderColor: 'rgb(75, 192, 192)', // Couleur de la ligne
                        tension: 0.1 // Tension de la courbe
                    }]
                }
            });
        }
        
        // Fonction de nettoyage qui sera appele lors du dmontage du composant
        return () => {
            // Vrifier s'il existe une instance de Chart.js
            if (chartInstance) {
                // Si oui, la dtruire pour librer les ressources
                chartInstance.destroy();
            }
        };
    }, [temperatures]); // Dclencher l'effet  chaque changement des donnes de temprature

    // Rendu du composant
    return (
        <div>
            <canvas ref={chartRef} /> {/* Rfrence  l'lment canvas */}
        </div>
    );
}

export default TemperatureChart;
